*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.containerz {
  width: 100%;
  margin: auto;
  padding: 0 20px;
}

.background-container {
  background: repeating-linear-gradient(#fff 0%, #fff 16%, #fff5f6 16%, #fff5f6 100%);
}

.contacts-section {
  margin-top: 60px;
}

.contacts-card-list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-bottom: 32px;
  list-style-type: none;
  display: flex;
}

.contacts-card {
  background-color: var(--white-text-color);
  text-align: left;
  width: 280px;
  height: auto;
  min-height: 412px;
  border-radius: 24px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 40px 40px 40px 32px;
  display: flex;
  box-shadow: 0 8px 30px rgba(212, 20, 67, .1);
}

.contacts-card:hover, .contacts-card:focus {
  box-shadow: 0 15px 25px rgba(0, 0, 0, .15), 0 5px 10px rgba(0, 0, 0, .05);
}

.type-of-location {
  color: #fff;
  text-align: center;
  letter-spacing: .48px;
  width: 80px;
  height: 16px;
  text-transform: uppercase;
  border-radius: 5px;
  justify-content: center;
  align-self: flex-start;
  align-items: center;
  margin-left: -5px;
  padding: 10px;
  font-size: 12px;
  font-weight: 500;
  line-height: 186%;
  display: flex;
}

.cafe-location-type {
  background-color: var(--yellow-color);
}

.foodtruck-location-type {
  background-color: var(--green-color);
  width: 130px;
}

.card-location-title {
  color: var(--black-text-color);
  letter-spacing: .48px;
  padding-bottom: 8px;
  font-family: DM Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 186%;
}

.card-location-and-description {
  border-bottom: solid 2px var(--light-gray-color);
  width: 216px;
  padding-bottom: 20px;
  display: block;
}

.card-location-description {
  color: var(--gray-text-color);
  letter-spacing: .48px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card-location-description::selection {
  background-color: var(--gray-text-color);
  color: #fff;
}

.fixed-lines {
  height: 2.4em;
  line-height: 1.2em;
  overflow: hidden;
}

.week-and-hours-availability {
  border-bottom: solid 2px var(--light-gray-color);
  width: 216px;
  padding-bottom: 20px;
  display: block;
}

.card-week-availability {
  color: var(--black-text-color);
  font-size: 12px;
  font-weight: 600;
  line-height: 186%;
}

.weekday-card-hours-availability {
  padding-bottom: 6px;
}

.card-hours-availability {
  color: var(--cream-text-color);
  font-size: 12px;
  font-weight: 600;
  line-height: 186%;
}

.contacts-card-link {
  color: var(--gray-text-color);
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 183%;
}

.contacts-card-link:hover, .contacts-card-link:focus {
  color: var(--red-text-color);
}

.card-address-container {
  width: 216px;
}

@media screen and (min-width: 768px) and (max-width: 1200px) {
  .background-container {
    background: repeating-linear-gradient(#fff 0%, #fff 30%, #fff5f6 30%, #fff5f6 100%);
  }

  .contacts-card-list {
    flex-direction: row;
    margin-bottom: 60px;
    display: flex;
  }

  .contacts-card {
    width: 224px;
    min-height: 528px;
    gap: 32px;
  }

  .type-of-location {
    margin-left: -5px;
    font-size: 14px;
  }

  .card-location-title {
    font-size: 14px;
  }

  .card-location-and-description {
    width: 159px;
  }

  .card-location-description {
    padding-bottom: 10px;
    font-size: 14px;
  }

  .week-and-hours-availability {
    width: 159px;
    padding-bottom: 24px;
  }

  .weekday-card-hours-availability {
    padding-bottom: 10px;
  }

  .card-week-availability, .card-hours-availability, .contacts-card-link {
    font-size: 14px;
  }

  .card-address-container {
    width: 159px;
  }
}

@media screen and (min-width: 1200px) {
  .background-container {
    background: repeating-linear-gradient(#fff 0%, #fff 10%, #fff5f6 10%, #fff5f6 100%);
  }

  .contacts-card-list {
    flex-direction: row;
    margin-bottom: 60px;
    display: flex;
  }

  .contacts-card {
    width: 368px;
    min-height: 538px;
  }

  .type-of-location {
    margin-bottom: 12px;
    margin-left: 0;
    font-size: 16px;
  }

  .card-location-title {
    font-size: 16px;
  }

  .card-location-and-description {
    width: 288px;
  }

  .card-location-description {
    padding-bottom: 10px;
    font-size: 16px;
  }

  .week-and-hours-availability {
    width: 288px;
    padding-top: 5px;
    padding-bottom: 20px;
  }

  .card-week-availability, .card-hours-availability {
    font-size: 16px;
  }

  .weekday-card-hours-availability {
    padding-bottom: 10px;
  }

  .contacts-card-link {
    font-size: 16px;
  }

  .card-address-container {
