.contacts-button-section {
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-bottom: 40px;
  display: flex;
}

.contacts-button {
  color: var(--white-text-color);
  text-align: center;
  width: 161px;
  min-height: 44px;
  cursor: pointer;
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font-size: 16px;
  font-weight: 700;
  line-height: normal;
  display: flex;
}

.map-button {
  background-color: var(--red-text-color);
}

.map-button-icon {
  fill: var(--black-text-color);
}

.franchise-button {
  background-color: var(--white-text-color);
  color: var(--red-text-color);
  transition-property: background-color, color;
  transition-duration: .25s;
  transition-timing-function: cubic bezier(.4, 0, .2, 1);
}

.franchise-button:hover, .franchise-button:focus {
  background-color: var(--red-text-color);
  color: var(--white-text-color);
}

.franchise-button:hover .open-franchise-icon, .franchise-button:focus .open-franchise-icon {
  stroke: var(--white-text-color);
}

.open-franchise-icon {
  stroke: var(--red-text-color);
}

@media screen and (min-width: 768px) and (max-width: 1200px) {
  .contacts-button-section {
    flex-direction: row;
    margin-bottom: 40px;
  }

  .contacts-button {
    width: 192px;
    height: 44px;
  }

  .map-button {
    transition-property: transform;
    transition-duration: .25s;
    transition-timing-function: cubic bezier(.4, 0, .2, 1);
  }

  .map-button:hover, .map-button:focus {
    transform: translateY(-50%);
  }

  .franchise-button {
    width: 174px;
    height: 44px;
    transition-property: transform;
    transition-duration: .25s;
    transition-timing-function: cubic bezier(.4, 0, .2, 1);
  }

  .franchise-button:hover, .franchise-button:focus {
    transform: translateY(-50%);
  }
}

@media screen and (min-width: 1200px) {
  .contacts-button-section {
    flex-direction: row;
    margin-bottom: 121px;
  }

  .contacts-button {
    width: 198px;
    height: 44px;
    flex-direction: row;
    display: flex;
  }

  .franchise-button {
    width: 168px;
    height: 44px;
  }

  .map-button {
    transition-property: transform;
    transition-duration: .25s;
    transition-timing-function: cubic bezier(.4, 0, .2, 1);
  }

  .map-button:hover, .map-button:focus {
    transform: translateY(-50%);
  }

  .franchise-button {
    width: 174px;
    height: 44px;
    transition-property: transform;
    transition-duration: .25s;
    transition-timing-function: cubic bezier(.4, 0, .2, 1);
  }

  .franchise-button:hover, .franchise-button:focus {
    transform: translateY(-50%);
  }
}

.footer-container {
  border-bottom: 2px solid var(--light-gray-color);
  padding-bottom: 40px;
}

.footer-title-part {
  margin-bottom: 40px;
}

.footer-first-title {
  color: var(--cream-text-color);
  text-transform: uppercase;
  font-family: Titan One, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: normal;
}

.footer-second-title {
  color: var(--red-text-color);
  letter-spacing: 1.36px;
  text-transform: uppercase;
  font-family: Titan One, sans-serif;
  font-size: 34px;
  font-weight: 400;
  line-height: normal;
  display: block;
}

.made {
  color: var(--cream-text-color);
  letter-spacing: 1.2px;
