.franchise-modal-window-container {
  width: 100%;
  height: 100%;
  background-color: rgba(46, 47, 66, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.franchise-modal-window {
  min-height: 500px;
  width: 392px;
  border: 4px solid var(--cream-text-color);
  font-weight: 600;
  font-family: var(--font-secondary);
  background-color: #fcfcfc;
  border-radius: 10px;
  padding: 72px 24px 24px;
  position: absolute;
  top: 50%;
  left: 50%;
