:root {
  --white-text-color: #fff;
  --red-text-color: #d41443;
  --cream-text-color: #ffa5ba;
  --black-text-color: #000;
  --gray-text-color: #907e82;
  --light-pink-color: #fff5f6;
  --products-first-color: #ffb8ca;
